import{BackgroundShape}from"../enums/background-shape";import{SettingsActionType}from"../enums/settings";export var defaultSettingsState={backgroundShape:BackgroundShape.SPHERE,showSettingsModal:false,backgroundAutoRotate:true,sectionsFade:true};export var settingsReducer=function settingsReducer(state,action){if(state===void 0){state=defaultSettingsState;}switch(action.type){case SettingsActionType.SET_BACKGROUND_SHAPE:return Object.assign({},state,{backgroundShape:action.payload});case SettingsActionType.SET_SHOW_SETTINGS_MODAL:return Object.assign({},state,{showSettingsModal:action.payload==="true"});case SettingsActionType.SET_BACKGROUND_AUTO_ROTATE:return Object.assign({},state,{backgroundAutoRotate:action.payload==="true"});case SettingsActionType.SET_SECTIONS_FADE:return Object.assign({},state,{sectionsFade:action.payload==="true"});default:return state;}};